#headerLogin {
  position: absolute;
  top: 0; }

#loginFormCont #headerLogin a {
  font-family: 'Open Sans Hebrew', serif;
  font-size: 13px; }

#loginFormCont #openLoginForm {
  margin: 36px 0; }

@media (max-width: 1200px) {
  #loginFormCont {
    z-index: 999; } }

@media (max-width: 768px) {
  #loginFormCont {
    margin: 0;
    left: 0;
    position: absolute;
    top: 98px; } }

@media (max-width: 768px) {
  #loginFormCont > div {
    float: left; } }

@media (max-width: 1200px) {
  #toggleLoginForm {
    position: fixed;
    min-width: 50vw;
    left: 0px;
    background-color: #fff;
    margin: 0 25vw;
    padding: 5vw;
    /* border-radius: 50px; */
    box-shadow: 0px 7px 5px -6px;
    border: 1px solid #204260; } }

@media (max-width: 768px) {
  #toggleLoginForm {
    position: fixed;
    min-width: 95vw;
    left: 0px;
    background-color: #fff !important;
    margin: 0 2.5vw;
    padding: 5vw;
    /* border-radius: 50px; */
    box-shadow: 0px 7px 5px -6px;
    border: 1px solid #204260; } }
