#home-aboutus h2 {
  font-weight: 700;
  color: #204260; }

#home-aboutus button {
  background-color: #e08222; }

@media (max-width: 768px) {
  #home-aboutus #home-aboutus_image {
    text-align: center; } }

#home-aboutus img {
  width: 180px;
  height: 180px;
  border-radius: 50%; }

#home-aboutus .rounded {
  border-radius: 50%;
  border: 2px solid #3bd2ff;
  width: 184px;
  height: 184px;
  margin-top: 30px; }
  @media (max-width: 768px) {
    #home-aboutus .rounded {
      margin: 0 auto; } }

@media screen and (max-width: 600px) {
  #front-faq button {
    margin-right: 4px;
    font-size: 13px; } }
