#bugContact .modal-content {
  border: 1px solid white !important; }

#bugContact .modal-header {
  background-color: #204260;
  color: white; }

#bugContact form {
  width: 80%;
  margin: 0 auto; }
  #bugContact form select {
    border-radius: 20px;
    box-shadow: none; }
  #bugContact form textarea {
    border-radius: 10px; }
  #bugContact form #submitBtn {
    text-align: center; }
    #bugContact form #submitBtn button {
      width: 200px; }
    #bugContact form #submitBtn button:hover {
      background-color: #204260; }

#bugContact .close {
  opacity: 1;
  color: white; }
  #bugContact .close span {
    font-size: 20px; }
