#FrontRegisterRow #formSectioCont {
  padding-right: 0px;
  position: relative;
  bottom: 80px;
  background-color: #204260;
  border-radius: 90px 0 0 90px; }
  @media (max-width: 768px) {
    #FrontRegisterRow #formSectioCont {
      border-radius: 0;
      box-shadow: 0px 7px 5px -6px; } }
  #FrontRegisterRow #formSectioCont h2 {
    color: #fff;
    font-size: 2rem; }
    #FrontRegisterRow #formSectioCont h2 span {
      font-size: 2rem;
      font-weight: 100; }
  #FrontRegisterRow #formSectioCont .form-group {
    width: 100%; }
  #FrontRegisterRow #formSectioCont .form-inline .form-control {
    width: 100%; }

#FrontRegisterRow .submit {
  background-color: #e08222;
  padding: 7px 25px;
  position: relative;
  bottom: 1px;
  font-weight: bold; }
  #FrontRegisterRow .submit:hover {
    background-color: #fff;
    color: #e08222; }

