#mainMenuRow {
  position: relative;
  top: 20px; }
  #mainMenuRow #TopNav {
    display: block; }
    #mainMenuRow #TopNav a.topNavMenuItems.black {
      font-size: 16px; }
    @media (max-width: 991px) {
      #mainMenuRow #TopNav .container {
        display: flex;
        flex-direction: column; }
      #mainMenuRow #TopNav .navbar-collapse {
        margin: 26px 0; } }
    @media (max-width: 768px) {
      #mainMenuRow #TopNav {
        padding-bottom: 25px; }
        #mainMenuRow #TopNav li.navbar-brand.logo.active {
          width: 100%;
          text-align: center; }
        #mainMenuRow #TopNav button.navbar-toggle {
          float: right;
          right: 0;
          top: 0px;
          background-color: #f2b502;
          border-radius: 50px; }
          #mainMenuRow #TopNav button.navbar-toggle span {
            background-color: #fff; }
        #mainMenuRow #TopNav .navbar-default .navbar-toggle {
          background-color: #f2b502; }
        #mainMenuRow #TopNav .navbar-collapse {
          border: none;
          box-shadow: none; }
        #mainMenuRow #TopNav li.facebook {
          text-align: center; }
        #mainMenuRow #TopNav ul.nav {
          background: #f5f5f547;
          border-radius: 10px;
          margin-top: 30px; } }

#mainMenuRow.showMsg {
  position: relative;
  top: 40px; }
