#footer-form {
  margin-bottom: 10px; }
  #footer-form .form-container {
    background-color: white;
    border-radius: 50px;
    padding-left: 0;
    max-width: 480px; }
  #footer-form .css-reset {
    margin: 0;
    padding: 0; }
    #footer-form .css-reset input {
      border: none;
      border-radius: 0;
      box-shadow: none !important;
      height: 33px !important; }
    #footer-form .css-reset .form-group {
      margin-bottom: 0; }
  #footer-form .pull-left {
    float: left; }
  #footer-form .newsLetterText {
    color: white; }
    #footer-form .newsLetterText p {
      margin: 0; }
  #footer-form button {
    margin-top: 4px;
    width: 70px;
    background-color: #e08222;
    border: none;
    color: white !important;
    font-weight: 700; }
  #footer-form ::-webkit-input-placeholder {
    color: #204260; }
  #footer-form ::-moz-placeholder {
    color: #204260; }
  #footer-form ::-ms-input-placeholder {
    color: #204260; }
  #footer-form ::placeholder {
    color: #204260; }
  #footer-form .bold {
    font-weight: 700; }
  #footer-form #formBasicEmail {
    border-radius: 0;
    border-right: 1px solid lightgray; }
  #footer-form .alert {
    background-color: #204260;
    border: none !important;
    padding: 0; }
  #footer-form .alert-danger {
    color: #ff0500 !important; }
  #footer-form .alert-success {
    color: #dff0d8 !important; }
